@import "variables";

@media (max-width: 1023px) {
  .content-slot-top {
    background: url("../images/bg-home1-sm.jpg") no-repeat center;
    height: calc(100vw * 1628/1440);
  }
}
@media (min-width: 1024px) {
  .content-slot-top {
    background: url("../images/bg-home1-lg.jpg") no-repeat center;
    height: calc(100vw * 872/1850);
  }
}
.content-slot-top {
  justify-content: center;
  background-size: cover;
  color: $text-color-white;
}
.product-slot {
  .second-row {
    flex-direction: row-reverse;
  }
  .product-box {
    padding: 7vw 1vw 4vw 1vw;
    transition: transform .5s;
  }
  .product-box:hover {
    transform: scale(1.05, 1.05);
  }
  .product-box1 {
    @extend .product-box;
    background-image: linear-gradient(233deg, #818080, #4F4F4F);
    color: $text-color-white;
  }
  .product-box2 {
    @extend .product-box;
    background-image: linear-gradient(233deg, #FFFFFF, #E9E9E9);
    color: #002a3a;
  }
}
.content-slot-mid {
  .text-slot {
    display: flex;
    justify-content: space-between;
    background-color: #1c5f4a;
    margin-top: -4px;
    color: rgb(216, 216, 216);
    @media (min-width: 1024px) {
      .text-x5 {
        font-size: calc(100vw * 1.3/27);
      }
    }
  }
}
.content-slot-bottom {
  margin-top: calc(100vw *3/27);
  margin-bottom: calc(100vw *2/27);
  flex-direction: row-reverse;
  @media (max-width: 1023px) {
    .heading {
      padding: calc(100vw * 2 / 27) 0 calc(100vw * 3 / 27) calc(100vw * 2 / 27);
    }
  }
  @media (min-width: 1024px) {
    .heading {
      padding: calc(100vw * 2/27) calc(100vw * 1.5/27) 0 0;
    }
  }
}
